/* General Header Styles */
.header {
  background-color: purple; /* Sets the background color of the header to purple */
  height: 70px; /* Specifies the height of the header */
  display: flex; /* Enables flexbox for layout management */
  justify-content: space-between; /* Aligns children with space between them (logo on one side, button on the other) */
  align-items: center; /* Vertically centers the items within the header */
  padding: 11px 20px; /* Adds 11px padding vertically and 20px horizontally */
}

.logo {
  height: 35px; /* Sets the height of the logo */
  background-color: #ff7f7f; /* Adds a light red background color behind the logo */
  border-radius: 4px; /* Rounds the corners of the logo background slightly */
}

/* Modal Styles */
.modal-box {
  background-color: white; /* Sets the modal background color to white */
  width: 400px; /* Defines the width of the modal box */
  margin: 100px auto; /* Centers the modal vertically with 100px margin from the top and horizontally using `auto` */
  padding: 20px; /* Adds space inside the modal for content */
  border-radius: 8px; /* Smoothly rounds the corners of the modal */
  outline: none; /* Removes the default focus outline when the modal is active */
}

.tab-content {
  margin-top: 20px; /* Adds spacing above the tab content to separate it from the tabs */
}
