/* Home Page Styling */
.home-container {
    width: 80%;
    margin: auto;
    margin-top: 20px;
    text-align: center;
  }
  
  /* Tabs Styling */
  .MuiTabs-root {
    background-color: #f5f5f5;
    border-radius: 8px;
    padding: 10px;
  }
  
  /* Tab Content Container */
  .tab-content {
    margin-top: 20px;
  }
  
  /* Doctor Card Styling */
  .doctor-card {
    padding: 20px;
    margin-bottom: 15px;
    border-radius: 8px;
    background: white;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
    text-align: left;
  }
  
  /* Doctor Name */
  .doctor-name {
    font-size: 18px;
    font-weight: bold;
    color: #333;
  }
  
  /* Doctor Specialization */
  .doctor-specialization {
    font-size: 16px;
    color: #666;
  }
  
  /* Doctor Rating */
  .doctor-rating {
    font-size: 14px;
    color: #ff9800;
  }
  
  /* Buttons */
  .action-buttons {
    margin-top: 10px;
    display: flex;
    gap: 10px;
  }
  
  /* Appointment List Styling */
  .appointment-card {
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 8px;
    background: #e3f2fd;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Login Message */
  .login-message {
    color: red;
    font-size: 16px;
    font-weight: bold;
  }
  